page-workers {
  .workers-content
  {
    background-color: color($colors, background, base);

    .workers-list
    {
      @include basic-list-styles($theme-color-3, $theme-color-2);

      .list-item {
        &--on-site {
          background: #f0e68c;
        }
        &--left-site {
          background: #cd5c5c;
        }
      }

      .worker-clock-on {
        font-size: 14px;
        margin-top: 8px;
        color: #228b22;
      }
    }
  }
}
