.timesheet {
  display: flex;
  flex-direction: row;
  padding: 16px 0;
  border-top: 1px dashed #777;

  &__checkmark, &__extended-hours {
    flex: 0 0 40px;
    display: flex;
    height: 40px;
    margin: 8px 16px 8px 0;
    justify-content: center;
    align-items: center;
    font-size: 2em;

    &--approvable {
      border: 1px solid #777;
    }
  }

  &__extended-hours {
    margin: 8px 0 8px 16px;
    border: 1px solid #777;
  }

  &__details {
    flex: 1 1 auto;
    min-width: 0;

    &--deleted {
      text-decoration: line-through;
    }
  }

  &__top-line, &__bottom-line {
    display: flex;
    flex-direction: row;
    align-items: center;
  }

  &__bottom-line {
    justify-content: space-around;
  }

  &__top-line {
    margin-bottom: 8px;
  }

  &__user {
    flex: 1 1 auto;
    min-width: 0;

    font-size: 2.2rem;
    color: $theme-color-1;
    text-overflow: ellipsis;
    overflow: hidden;
  }

  &__timing {
    flex: 0 0 auto;
  }

  &__start-date, &__end-date {
    flex: 0 1 80px;
    min-width: 0;
    color: #777;
    text-align: center;
    margin: 0 4px;
  }

  &__lunch {
    flex: 0 0 auto;
    padding: 8px 8px;
    color: $theme-color-3;
    width: 32px;
    height: 32px;

    &--approvable {
      border: 1px solid #777;
    }
  }

  &__extended-hours-hint {
    font-size: 10px;
    color: #eee;
  }

  .datetime {
    padding: 8px 0 16px 0;
  }

  .item-datetime {
    padding-left: 0;
  }
}
