.timesheet-site-select {
  height: 64px;
}

.timesheet-verify-content {
  // margin-top: 52px;
  display: block;
}

.timesheet-verify-filter {
  display: flex;
  flex-direction: row;
  align-items: center;

  site-select {flex: 1 1 auto; min-width: 0;}
  &__date {
    flex: 0 0 auto;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    position: relative;
  }
  &__date-hint {
    color: #777;
    font-size: 1rem;
  }
}

.timesheet-verify-toolbar {
  display: flex;
  flex-direction: row;
  align-items: center;
}
